.format-toolbar {
  display: flex;
  border-bottom: solid 1.7px rgba(199, 198, 255, 0.15);
  padding: 10px 0;
  margin: 0 0 10px 0;
}

blockquote {
  background: #f9f9f9;
  border-left: 4px solid #ccc;
  margin: 1.5em 10px;
  padding: 0.5em 10px;
}

div[data-slate-editor] {
  flex-grow: 1;
  max-height: 100%;
  overflow-y: scroll;
  height: 0px;
  border-bottom: solid 1.7px rgba(199, 198, 255, 0.15);
}
